import React from "react"
import { Navigate, Route, Routes } from "react-router-dom"

import Leagues from "../leagues"
import Teams from "../teams"

export default function Navigation() {
  return (
    <Routes>
      <Route path="/" element={<Navigate to="/soccer-stat/leagues" />} />
      <Route
        path="/soccer-stat/"
        element={<Navigate to="/soccer-stat/leagues" />}
      />
      <Route path="/soccer-stat/leagues" element={<Leagues />} />
      <Route path="/soccer-stat/teams" element={<Teams />} />
    </Routes>
  )
}
